
body{
    background-color: bisque;
}

form{
display: flex;
flex-direction: column;
align-content: center;
margin: 1rem;
}

h1{
    margin-left: 1rem;
    color: rgba(65, 58, 25, 0.816);
}
h2{
    color:  rgba(65, 58, 25, 0.816);
    border-bottom: 1.5px solid  rgba(155, 210, 192, 0.402);;
}
.counter-text{
    width: 16rem;
    height: 1.4rem;
    margin: 10px;
    border-radius: 5px;
    border-style: none;
}
button{
    width: 7rem;
    height: 2rem;
    background-color: rgba(155, 210, 192, 0.402);
    color: rgb(245, 6, 6);
    border-radius: 5px;
    border-style: none;
}
button:hover{
    background-color: rgb(248, 7, 7);
    color: aliceblue;
}

.counter-main{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    height: auto;
}
.counter-list{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-content: center;
    width: 13rem;
    height: 13rem;
}
.separate-button{
    display: grid;
    place-items: center;
}
.counter-info{
    background-color: antiquewhite;
    margin: 9px;
    border-radius: 10px;
    height: 15rem;
    display: flex;
    flex-direction: column;
}
.button-form:hover{
    background-color: aquamarine;
}
.button-form{
color: rgba(33, 33, 243, 0.685);
margin-left: 4.5rem;
}
